import React from 'react';
import ReactDOM from 'react-dom';
import './styles/index.css';

import Header from './components/Header'
import Menu from './components/Menu'

import Main from './components/contentComponents/Main'
import About from './components/contentComponents/About'
import Catalog from './components/contentComponents/Catalog'
import Services from './components/contentComponents/Services'
import Contacts from './components/contentComponents/Contacts'

import Basement from './components/Basement'


type MyProps = {}
type MyState = { isContent: string }

