class App extends React.Component<MyProps, MyState> {
  constructor(props: any) {
    super(props);
    this.handleMainClick = this.handleMainClick.bind(this);
    this.handleAboutClick = this.handleAboutClick.bind(this);
    this.handleCatalogClick = this.handleCatalogClick.bind(this);
    this.handleServicesClick = this.handleServicesClick.bind(this);
    this.handleContactsClick = this.handleContactsClick.bind(this);
    this.state = { isContent: 'main' };
  }

  changeBackground(flag: boolean): void {
    let typeBackground: string, delBackground: string;
    flag ? typeBackground = '_mainBackground' : typeBackground = '_addBackground';
    flag ? delBackground = '_addBackground' : delBackground = '_mainBackground';
    const content = document.getElementsByClassName('mainContainer');
    for (let i = 0; i < content.length; i++) {
      content[i].classList.remove(delBackground);
      content[i].classList.add(typeBackground);
    }
  }

  handleMainClick() {
    this.setState({ isContent: 'main' });
    this.changeBackground(true);
  }
  handleAboutClick() {
    this.setState({ isContent: 'about' });
    this.changeBackground(false);
  }
  handleCatalogClick() {
    this.setState({ isContent: 'catalog' });
    this.changeBackground(false);
  }
  handleServicesClick() {
    this.setState({ isContent: 'services' });
    this.changeBackground(false);
  }
  handleContactsClick() {
    this.setState({ isContent: 'contacts' });
    this.changeBackground(false);
  }

  render() {
    const isContent = this.state.isContent;
    let content;

    switch (isContent) {
      case 'main':
        content = <Main class='content main' />
        break;
      case 'about':
        content = <About class='content about' />
        break;
      case 'catalog':
        content = <Catalog class='content catalog' />
        break;
      case 'services':
        content = <Services class='content services' />
        break;
      case 'contacts':
        content = <Contacts class='content contacts' />
        break;
      default:
        content = <Main class='content main' />
        break;
    }

    return (
      <div className="container" >
        <Header />
        <div className="mainContainer _mainBackground">
          <Menu mainOnClick={this.handleMainClick}
            aboutOnClick={this.handleAboutClick}
            catalogOnClick={this.handleCatalogClick}
            servicesOnClick={this.handleServicesClick}
            contactsOnClick={this.handleContactsClick}
          />
          {content}
        </div>
        <Basement />
      </div>
    );
  }
}

ReactDOM.render(
  <App />,
  document.getElementById('root')
);
