function FeedbackComp() {
  return (
    <div className="feedbackComp" >
      <h5>Напишите нам</h5>
      <h6>Ваше имя</h6>
      <h6>Ваша фамилия</h6>
      <h6>Email</h6>
      <h6>Телефон</h6>
      <h6>Компания</h6>
      <h6>Сообщение</h6>
      <textarea rows={9} maxLength={200} name="feedback_text" id="feedback_text"/>
      <button type='submit' id='submit'>Отправить</button>
    </div>
  )
}

export default FeedbackComp
