import React from 'react'
import ContactsComp from './contactsComponents/ContactsComp'
import FeedbackComp from './contactsComponents/FeedbackComp'
import MapComp from './contactsComponents/MapComp'
import '../../styles/contacts.css'

function Contacts(props: any) {
  return (
    <div className={props.class} >
      <div className='firstSec'>
        <ContactsComp />
        <MapComp className='mapComp' width='474px' height='315px' />
        <MapComp className='mapComp _hidden' width='80vw' height='315px' />
        <MapComp className='mapComp _hidden1' width='1000px' height='315px' />
      </div>
      <div className='secondSec'>
        <FeedbackComp />
      </div>
    </div>
  )
}

export default Contacts
