import React from 'react'
import PumpAnimated from './pumpComponents/PumpAnimated'
import PumpAnimatedCable from './pumpComponents/PumpAnimatedCable'
import PumpBody from './pumpComponents/PumpBody'
import '../../styles/pump.css'

function Pump() {
  return (
    <div className='pump' style={{
      margin: '20px',
      padding: '0px',
    }}>
      <PumpAnimatedCable />
      <PumpAnimated />
      <PumpBody />
    </div>
  )
}

export default Pump
