import React from 'react'
import '../styles/menu.css'

function Menu(props: any) {
  return (
    <div className='navigation' id='navigation'>
      <ul className='menu'>
        <li><h2 onClick={props.mainOnClick} className='anchor'>главная</h2></li>
        <li><h2 onClick={props.aboutOnClick} className='anchor'>о компании</h2></li>
        <li><h2 onClick={props.catalogOnClick} className='anchor'>каталог</h2></li>
        <li><h2 onClick={props.servicesOnClick} className='anchor'>услуги</h2></li>
        <li><h2 onClick={props.contactsOnClick} className='anchor'>контакты</h2></li>
      </ul>
    </div>
  )
}

export default Menu
