import React from 'react'
import Icon from './headerComponents/Icon'
import Pump from './headerComponents/Pump'


function Header() {
  return (
    <div className='header' style={{
      backgroundColor: 'black',
      height: '110px',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'space-between',
      boxShadow: '0px 13px 17px rgba(0, 0, 0, 0.25)',
      position: 'relative'
    }}>
      <Icon />
      <h1>ПБИ ПермьБурИнжиниринг</h1>
      <Pump />
    </div>
  )
}

export default Header
