import React from 'react'

function Basement() {
  return (
    <div style={{
      backgroundColor: 'black',
      height: '75px',
      position: 'relative',
      width: `${document.documentElement.clientWidth}`,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center'
    }}>
      <h4 style={{
        margin: '0px',
        padding: '0px',
      }}>Made by Artem and Maxim</h4>
    </div>
  )
}

export default Basement
